@import './abstracts/mixins';
@import './abstracts/colours';
@import './abstracts/breakpoints';

html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;
}

header {
  display: flex;
  justify-content: space-between;
  background-color: teal;
  color: white;
  height: 20vh;

  .logo {
    margin: 4vh 0 0 4vh;
    border-radius: 50%;
  }

  .hamburger {
    margin: 4vh 4vh 0 0;
  }

  .header-nav {
    ul {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      list-style: none;
      background-color: white;
      color: black;
    }
  }
}

.section-login,
.section-signup {
  background-color: teal;
  color: white;
  height: 90vh;
}

.form-login,
.form-signup,
.login-success,
.signup-success,
.profile-settings-section,
.error {
  @include centeredFlexColumn;
  // background-color: teal;
  // color: white;
  // height: 100vh;
}

.form-profile-settings {
  @include centeredFlexColumn;
}

.header {
  width: 100vw;
  height: 10vh;
  
  .hamburger {
    margin: 5vh;
  }
}

.homepage {
  // background-color: $teal;
  main {
    @include centeredFlexColumn;

    .homepage-first-view {
      @include centeredFlexColumn;
      position: relative;
      background-image: url('images/colleagues-highfive.jpg');
      background-position: 0px 0; 
      background-size: cover;
      height: 50vh;
      width: 100vw;
      @media (min-width: $small-mobile) and (max-width: #{$medium-mobile + 1px}) {
        background-position: -30px 0; 
      }

      @media (max-width: $small-mobile) {
        background-position: -60px 0; 
      }

      @media (min-width: $desktop) {
        background-image: url('images/colleagues-highfive-desktop.jpg');
        height: 100vh;
      }

      .homepage-text {
        position: absolute;
        top: 55vh;
        @include centeredFlexColumn;

        p {
          width: 60vw;
          text-align: center;
          margin-bottom: 0;
          @media (max-width: $small-mobile) {
            width: 70vw;
          }

          em {
            color: $teal;
            font-style: normal;
            @media (min-width: $desktop) {
              background-color: $teal;
              color: white;
              border-radius: 25px;
              padding: 0 5px;
              font-style: italic;
            }
          }
        }

        .signup-link button {
          background-color: $yellow;
          color: black;
          border: none;
          // border-radius: 25px;
          padding: 10px;
        }

        .login-link button,
        .api-link button {
          background-color: white;
          padding: 7px;
          border: 3px solid $yellow;
        }
      }
    }
  }

  .logo {
    width: 60vw;
    border-radius: 50%;
    transform: translateY(24vh);
    @media (min-width: $desktop) {
      width: 20vw;
      transform: translateY(-20vh);
    }
  }
}

.form-login,
.form-signup {
  .required:after {
    content:' *';
    color: red;
  }

  input {
    width: 40vw;
    @media (min-width: $desktop) {
      width: 15vw;
    }
  }
}

.error {
  background-color: $teal;
  height: 100vh;
  z-index: 1;
}

.login-success,
.signup-success {
  height: 100vh;

  button {
    margin: 5vh;
  }
}

.profile-settings-section {
  img {
    height: 10vh;
    border-radius: 50%;
  }
}

button {
  @include buttonStyle;
}
